@import url("https://fonts.googleapis.com/css2?family=Inter:wght@200;300;400;500;600;700&display=swap");
@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  input[type="number"]::-webkit-inner-spin-button,
  input[type="number"]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }
}

@layer components {
  .card {
    @apply bg-white text-gray-700
    basis-4/5 md:basis-2/5 lg:basis-1/5 min-h-[10rem]
    shadow-lg rounded-md overflow-hidden;
  }
  .badge {
    @apply px-3 py-1 rounded-full text-xs bg-green-400;
  }
  .product-title {
    @apply font-bold text-2xl overflow-ellipsis overflow-hidden whitespace-nowrap;
  }
  .btn-primary {
    @apply bg-black text-white hover:bg-white hover:text-black  border-black border py-3 rounded-full px-5;
  }
  .card-item {
    @apply flex flex-col w-full md:h-44 items-center bg-white  md:flex-row   dark:border-gray-700 dark:bg-gray-800 dark:hover:bg-gray-700;
  }
}
